import React, { Component } from 'react'

import '../resources/styles/Question.css'

export default class Question extends Component {
    render() {
        console.log(this.props.image)
        return (
            <div className="mainDiv">
                <div className="thirdCard">
                </div>
                <div className="secondCard">
                </div>
                <div className="frontCard">
                    <div class="row">
                            <label for="QuizNo">{this.props.QuizNo}/10</label>
                            <progress id="QuizNo" value={this.props.QuizNo} max="10"></progress>
                    </div>
                    {this.props.isImage ? (
                         <div class="row">
                            <div class="column">
                                <h1 className="questionTextAM" dangerouslySetInnerHTML={{__html: this.props.question}}></h1>
                            </div>
                            <div class="column">
                                <img className="questionImage" src={this.props.image.default} alt="ImagineIntrebare"></img>
                            </div>
                        </div> 
                    ) : (
                    <div class="row">
                        <h1 className="questionText" dangerouslySetInnerHTML={{__html: this.props.question}}></h1>
                    </div> 
                )}
                   
                </div>
            </div>
        )
    }
}
