import React, { Component } from 'react'
import logo from '../resources/images/Logo.png'

import '../resources/styles/Logo.css'

export default class Logo extends Component {
    render() {
        return (
            <div>
                <a href="/main_menu">
                    <img 
                        className={this.props.cls} 
                        src={logo} 
                        alt="Logo"
                    ></img>
                </a>
            </div>
        )
    }
}
