import React, { Component } from 'react'

import Button from './StartButton.jsx'

import "../resources/styles/Categories.css"

export default class Categories extends Component {
    render() {
        return (

            <div className="cardCategories">
                <h1 className="cardTitle">Categories</h1>
                <Button
                    cls="btnCategories"
                    href='/quiz'
                    textBtn='Computer Science'
                />
                <Button
                    cls="btnCategories"
                    href='/quizAM'
                    textBtn='Aplicatii Multimedia'
                />
            </div>
        )
    }
}
